import NewsCard from '@/components/Common/NewsCard'
import {collegeNews} from "@/api/college"
export default {
  components:{
    NewsCard
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      page:1,
      pageSize:10,
    }
  },
  methods: {
    async onLoad() {
      if (this.refreshing) {
        this.list = []
        this.refreshing = false
        this.page = 1
      }
      const { data } = await collegeNews({
        id:32,
        info: { page: this.page++, pageSize: this.pageSize }
      })
      this.loading = false
      if(data.code === 0){
        this.list = this.list.concat(data.data.entries)
        if(+data.data.total === this.list.length) this.finished = true
      }
    },
    onRefresh() {
      this.finished = false
      this.loading = true
      this.onLoad()
    },
  },
  head () {
    return {
      title: '高校公益行往期回顾-艾思云课堂',
    }
  },
}
